"use server";

import { checkAvailability, saveBooking, Booking } from "@/lib/db-local";
import { addMyStudioAction, getUserById } from "./user";
import { fetchStudio } from "./studio";
import { createPayment } from "./payment";
import { Resend } from 'resend';

const resend = new Resend(process.env.RESEND_API_KEY);

// Update interface
interface BookingRequest {
    userId: string;
    studioId: string;
    roomName?: string;
    date: string;
    startTime: string;
    durationHours: number;
    userCount: number;
    equipmentIds: string[];
    totalPriceOverride?: number; // Allow client calculated price
}

// ... constants ...
const PRICE_BAND_HOURLY = 2500;
const PRICE_INDIVIDUAL_HOURLY = 800;
const PRICE_LOCKOUT_FLAT = 20000;

interface BookingResponse {
    success: boolean;
    message: string;
    price?: number;
    bookingId?: string;
}

export async function createBooking(data: BookingRequest): Promise<BookingResponse> {
    const { userId, studioId, roomName, date, startTime, durationHours, userCount, totalPriceOverride } = data;

    // ... availability check ...

    // 1. INVENTORY CHECK (Double Booking Prevention)
    const isAvailable = checkAvailability(studioId, roomName, date, startTime, durationHours);
    if (!isAvailable) {
        return {
            success: false,
            message: "üö´ This time slot is already booked! Please choose another time."
        };
    }

    // 2. Calculate Price
    let totalPrice = 0;
    if (totalPriceOverride !== undefined) {
        totalPrice = totalPriceOverride;
    } else {
        // Fallback to legacy logic
        const bookingDate = new Date(date);
        const now = new Date();
        const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
        const tomorrow = new Date(today);
        tomorrow.setDate(tomorrow.getDate() + 1);
        const targetDate = new Date(bookingDate.getFullYear(), bookingDate.getMonth(), bookingDate.getDate());
        const isTodayOrTomorrow = targetDate.getTime() === today.getTime() || targetDate.getTime() === tomorrow.getTime();

        if (durationHours >= 10) {
            totalPrice = PRICE_LOCKOUT_FLAT;
        }
        else if (userCount <= 2 && isTodayOrTomorrow) {
            totalPrice = PRICE_INDIVIDUAL_HOURLY * userCount * durationHours;
        }
        else {
            totalPrice = PRICE_BAND_HOURLY * durationHours;
        }
    }

    // 3. Save to Local DB
    const newBooking: Booking = {
        id: Math.random().toString(36).substring(7),
        userId,
        studioId,
        roomName,
        date,
        startTime,
        durationHours,
        userCount,
        totalPrice,
        status: 'active',
        createdAt: new Date().toISOString()
    };

    saveBooking(newBooking);

    // 4. Create Payment Record (for revenue tracking)
    // ... same as before
    try {
        const studio = await fetchStudio(studioId);
        const user = await getUserById(userId);

        await createPayment({
            bookingId: newBooking.id,
            studioId: studioId,
            studioName: studio?.storeName || "Unknown Studio",
            userName: user?.name || "Guest User",
            userEmail: user?.email || "",
            userPhone: user?.phone,
            amount: totalPrice,
            paymentMethod: "stripe" // Default for online bookings
        });
    } catch (e) {
        console.error("Failed to create payment record", e);
    }

    // 5. Auto-register to My Studios
    // ...
    try {
        await addMyStudioAction(studioId);
    } catch (e) {
        console.error("Failed to auto-add my studio", e);
    }

    // 6. Notifications
    // ...
    const user = await getUserById(userId);
    const userEmail = user?.email;

    if (process.env.RESEND_API_KEY && userEmail) {
        try {
            await resend.emails.send({
                from: 'Studi-Go <noreply@send.studi-go.com>',
                to: userEmail,
                subject: '„ÄêMusic Studio„Äë‰∫àÁ¥ÑÂÆå‰∫Ü„ÅÆ„ÅäÁü•„Çâ„Åõ',
                html: `
                    <h1>‰∫àÁ¥Ñ„ÅåÂÆå‰∫Ü„Åó„Åæ„Åó„Åü</h1>
                    <p>‰ª•‰∏ã„ÅÆÂÜÖÂÆπ„Åß‰∫àÁ¥Ñ„ÇíÂèó„Åë‰ªò„Åë„Åæ„Åó„Åü„ÄÇ</p>
                    <ul>
                        <li><strong>‰∫àÁ¥ÑID:</strong> ${newBooking.id}</li>
                        <li><strong>„Çπ„Çø„Ç∏„Ç™:</strong> ${studioId}</li>
                        <li><strong>ÈÉ®Â±ã:</strong> ${roomName}</li>
                        <li><strong>Êó•‰ªò:</strong> ${date}</li>
                        <li><strong>ÊôÇÈñì:</strong> ${startTime} (${durationHours}ÊôÇÈñì)</li>
                        <li><strong>ÊñôÈáë:</strong> ¬•${totalPrice.toLocaleString()}</li>
                    </ul>
                    <p>„ÅîÊù•Â∫ó„Çí„ÅäÂæÖ„Å°„Åó„Å¶„Åä„Çä„Åæ„Åô„ÄÇ</p>
                `
            });
            console.log(`[Notification] üìß Email sent to ${userEmail} via Resend.`);
        } catch (error) {
            console.error('[Notification] ‚ùå Failed to send email via Resend:', error);
        }
    }

    return {
        success: true,
        message: "Booking successful! Confirmation sent to email/SMS.",
        price: totalPrice,
        bookingId: newBooking.id
    };
}

export async function cancelBookingAction(bookingId: string): Promise<{ success: boolean; message: string }> {
    const { updateBooking, getBookingById } = await import("@/lib/db-local");
    const booking = getBookingById(bookingId);
    if (!booking) return { success: false, message: "Booking not found" };

    const success = updateBooking(bookingId, { status: 'cancelled' });
    if (success) {
        console.log(`[Notification] üè™ Studio ${booking.studioId}: Booking ${bookingId} has been CANCELLED by user.`);
        // Here you would also update payment status to 'failed' or 'refunded' if needed
        return { success: true, message: "Booking cancelled successfully" };
    }
    return { success: false, message: "Failed to cancel booking" };
}

export async function updateBookingAction(bookingId: string, data: Partial<BookingRequest>): Promise<BookingResponse> {
    const { updateBooking, getBookingById, checkAvailability } = await import("@/lib/db-local");
    const oldBooking = getBookingById(bookingId);
    if (!oldBooking) return { success: false, message: "Booking not found" };

    const studioId = data.studioId || oldBooking.studioId;
    const roomName = data.roomName || oldBooking.roomName;
    const date = data.date || oldBooking.date;
    const startTime = data.startTime || oldBooking.startTime;
    const durationHours = data.durationHours || oldBooking.durationHours;

    // Check availability (excluding this specific booking)
    const isAvailable = checkAvailability(studioId, roomName, date, startTime, durationHours, bookingId);
    if (!isAvailable) {
        return { success: false, message: "üö´ This time slot is already booked!" };
    }

    const success = updateBooking(bookingId, {
        ...data,
        status: 'modified'
    });

    if (success) {
        console.log(`[Notification] üè™ Studio ${studioId}: Booking ${bookingId} has been MODIFIED by user.`);
        return { success: true, message: "Booking updated successfully" };
    }

    return { success: false, message: "Failed to update booking" };
}

export async function fetchMyBookings(): Promise<any[]> {
    const { getCurrentUser } = await import("./login");
    const { getBookings } = await import("@/lib/db-local");
    const { getAllStudios } = await import("@/lib/db-studio");
    const fs = await import("fs");
    const path = await import("path");

    const user = await getCurrentUser();
    if (!user) return [];

    const studios = getAllStudios();
    const studioMap = new Map(studios.map(s => [s.id, s.storeName]));

    // Fetch payments to check status
    let payments: any[] = [];
    try {
        const pPath = path.join(process.cwd(), "data", "payments.json");
        if (fs.existsSync(pPath)) {
            payments = JSON.parse(fs.readFileSync(pPath, "utf-8"));
        }
    } catch (e) {
        console.error("Failed to load payments for status check", e);
    }
    const paymentStatusMap = new Map(payments.map(p => [p.bookingId, p.status]));

    return getBookings()
        .filter(b => b.userId === user.id)
        .sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime())
        .map(b => ({
            ...b,
            studioName: studioMap.get(b.studioId) || "Unknown Studio",
            paymentStatus: paymentStatusMap.get(b.id) || "pending"
        }));
}
